
























import {
  reactive,
  defineComponent,
  toRefs,
  useRoute,
} from "@nuxtjs/composition-api";
import { messageState, currentUserInfo } from "~/store";
import { useMessage } from "~/store/message";
import { MessageType } from "~/store/types";

export default defineComponent({
  setup() {
    const state: MessageType = reactive({
      price: "",
      message: "",
      senderID: currentUserInfo._id,
      receiverID: messageState.receiverID,
    });

    const route = useRoute();
    const asked_service_id = route.value.params.id;
    const { createMessage,createOffer } = useMessage();

    const emptyForm = (): void => {
      state.price = "";
      state.message = "";
    };

    const submit = (): void => {
      createMessage(
        asked_service_id,
        state.price,
        state.message,
        state.senderID,
        state.receiverID
      );
      createOffer(
        state.price,
        state.receiverID,
        state.senderID,
        asked_service_id
      );
      emptyForm();
    };

    return {
      ...toRefs(state),
      submit,
    };
  },
});
